define('barapp/controllers/edit', ['exports', 'ember'], function (exports, Ember) {

  'use strict';

  exports['default'] = Ember['default'].Controller.extend({
    actions: {
      saveStatus: function saveStatus() {
        this.get("model").save();
        this.transitionToRoute("show", this.get("model"));
      }
    }
  });

});